import { useState } from "react";
import { FaBolt } from "react-icons/fa";
import { apiUrl } from "../constants";
import s from "../style/style.module.sass";

export function FallbackImg(    props) {
    const [error, setError] = useState(false);

    function onerror(ev)  {
        setError(true);
    }

    var src2 = props.src2 || apiUrl+"/assets/img/gnomed.png";

    return(
        <div>
            <img src={error?src2:(props.src||src2)} onError={onerror} style={{filter:error?"brightness(50%)":""}}></img>
            {error && 
                <div style={{color: "red", width: "40px", height:"50px"}} className={s.centerAbsolute}>
                    <FaBolt   size="30px"></FaBolt>
                    <div>URL</div>
                </div>
            }
        </div>
    );
}

export function FallbackImgImg(props) {
    const [error, setError] = useState(false);

    function onerror(ev)  {
        setError(true);
    }

    var src2 = props.src2 || apiUrl+"/assets/img/gnomed.png";
    console.log("src", props.src)
    console.log("src2", src2)
    var pass = {...props};
    delete pass.src;

    return(
        <div className={s.fallbackImg}><img src={ error?src2:(props.src||src2) } onError={onerror} {...pass}></img></div>
    );
}